<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入关键词"
                      @keyup.native.enter="initData"
                      v-model="categoryQuery.queryStr">
            </el-input>
            <el-button class="filter-item" @click="initData" size="small">查询</el-button>
            <el-button type="primary" @click="showAddDialog" v-if="permission.add" size="small">新增</el-button>
        </div>
        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="index" label="序号" width="110"></el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="120">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="getCategory(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id)" v-if="permission.delete">删除
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="categoryQuery.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="categoryQuery.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="修改限价信息" :visible.sync="dialogVisible" @close="dialogClose" :close-on-click-modal="false">
            <el-form :model="data" :rules="rules" ref="data">
                <el-form-item :label-width="itemWidth" label="*交易类型" prop="tradeMappingId">
                    <el-select v-model="data.tradeMappingId" placeholder="请选择">
                        <el-option
                                v-for="item in mappingList"
                                :key="item.id"
                                :label="item.baseName + '-'+ item.tradeName"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最低价格" prop="lowestPrice">
                    <el-input class="input-short" v-model="data.lowestPrice"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最高价格" prop="highestPrice">
                    <el-input class="input-short" v-model="data.highestPrice"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*涨跌比例" prop="ratio">
                    <el-input class="input-short" v-model="data.ratio"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="validate">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';
    import {validateEightDecimal} from '@/utils/validate';

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('不能为空'));
                }
                callback();
            };
            let validateDecimal = (rule, value, callback) => {
                if (value === '' || !validateEightDecimal(value) || value * 1 > 999999999 || value * 1 < 0.00000001) {
                    callback(new Error('请输入数值，最大值999999999，最小值0.00000001'));
                }
                callback();
            };
            return {
                itemWidth: '160px',
                dialogVisible: false,
                data: {
                    id: '',
                    tradeMappingId: '',
                    lowestPrice: '',
                    highestPrice: '',
                    ratio: ''
                },
                action: '',
                rules: {
                    tradeMappingId: [{validator: validate, trigger: 'blur'}],
                    lowestPrice: [{validator: validateDecimal, trigger: 'blur'}],
                    highestPrice: [{validator: validateDecimal, trigger: 'blur'}],
                    ratio: [{validator: validateDecimal, trigger: 'blur'}]
                },
                categoryQuery: {
                    queryStr: '',
                    pageNum: 1,
                    pageSize: 10
                },
                total: 0,
                tableData: [],
                tableHeader: [{
                    label: "法币名称",
                    prop: "baseName",
                    width: 245
                }, {
                    label: "交易币名称",
                    prop: "tradeName",
                    width: 245
                }, {
                    label: "最低价格",
                    prop: "lowestPriceString",
                    width: 245
                }, {
                    label: "最高价格",
                    prop: "highestPriceString",
                    width: 240
                }, {
                    label: "涨跌比例",
                    prop: "ratio",
                    width: 360
                }],
                mappingList: []

            }
        },
        created() {
            this.initData();
            this.getMapping();
        },
        methods: {
            addCategory() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademaplimit',
                        method: 'post',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showAddDialog() {
                this.dialogVisible = true;
                this.action = 'add';
            },
            validate() {
                this.$refs['data'].validate((valid) => {
                    if (valid) {
                        switch (this.action) {
                            case 'add':
                                this.addCategory();
                                break;
                            default:
                                this.modify();
                                break;
                        }
                    }
                });
            },
            dialogClose() {
                this.resetData();
            },
            setData(data) {
                let key;
                for (key in this.data) {
                    this.data[key] = data[key] === null ? '' : data[key];
                }
            },
            resetData() {
                let key;
                this.$refs['data'].resetFields();
                for (key in this.data) {
                    switch (key) {
                        default:
                            this.data[key] = '';
                    }
                }
                this.action = '';
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/trademaplimit',
                        method: 'get',
                        params: _this.categoryQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            getCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademaplimit/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setData(response.data);
                            _this.dialogVisible = true;
                        }
                    }).catch();
                })
            },
            deleteCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademaplimit/' + id,
                        method: 'delete'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showDeleteDialog(id) {
                this.$confirm('确认删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteCategory(id);
                }).catch(() => {
                    this.$message.info('操作取消');
                });
            },
            modify() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademaplimit',
                        method: 'put',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            query() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademaplimit/query',
                        method: 'get',
                        params: _this.categoryQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            initData() {
                if (this.categoryQuery.queryStr === '') {
                    this.getList();
                } else {
                    this.query();
                }
            },
            //翻页的按钮
            userPage(val) {
                this.categoryQuery.pageNum = val;
                this.initData();
            },
            //获取每页多少条
            userPageSize(val) {
                this.categoryQuery.pageSize = val;
                this.initData();
            },
            getMapping() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/trademapping/1/query',
                        method: 'post',
                        data: {
                            page: {
                                pageNum: 1,
                                pageSize: 100
                            }
                        }
                    }).then(function (response) {
                        if (response.result) {
                            _this.mappingList = response.data.data;
                        }
                    }).catch();
                })
            }
        }
    }
</script>



